@import url('https://fonts.googleapis.com/css2?family=Miriam+Libre:wght@400;700&display=swap');

:root {
    --white: #ffffff;
    --black: #31344b;
    --green: #16ce7f;
    --gray: #ececec;
    --box-shadow: 0px 0px 10px 0px rgba(34, 33, 33, 0.2);
    --radius: 10px;
    --color: #d1d2da;
}

*, *::before, *::after {
    box-sizing: border-box;
    font-family: 'Miriam Libre', sans-serif;
    margin: 0;
    padding: 0;
}

html {
    scroll-behavior: smooth;
}

body {
    padding: 20px;
}

main {
    min-width: 600px;
    max-width: 900px;
    margin: 0 auto;
    margin-bottom: 60px;
}

header {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 80px 0px 100px 0px;
}

h1 {
    font-size: 40px;
    padding-top: 8px;
}

h2 {
    font-size: 30px;
    margin-bottom: 40px;
}

h3 {
    font-size: 24px;
}

p {
    font-size: 20px;
    margin-bottom: 20px;
}

img {
    width: 60px;
    height: 60px;
    margin-right: 2px;
}

section {
    margin-bottom: 80px;
}

svg {
    display: block;
    margin: 0 auto;
}

.total__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 30px;
    margin-top: 40px;
}

strong {
    color: var(--green);
}

.chart {
    border-radius: var(--radius);
    box-shadow: var(--box-shadow);
    padding: 20px;
    margin: 40px 0;
    position: relative;
}

.chart__title {
    font-size: 24px;
    font-weight: bold;
    margin: 20px 0;
    text-align: center;
}

.chart .domain {
    stroke-width: 2px;
}

.chart .tick line {
    stroke: var(--black);
}

.chart .tick text {
    font-size: 12px;
    font-weight: bold;
}

.chart__legend__option {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding-left: 20px;
}

.chart__legend__option div {
    border-radius: 2.5px;
    min-width: 12px;
    min-height: 12px;
    margin-right: 4px;
}

.chart__legend {
    position: absolute;
    top: 80px;
}

.chart__legend__option:first-child div {
    background-color: var(--green);
    opacity: 0.4;
}

.chart__legend__option:last-child div {
    background-color: #d1d2da;
    opacity: 0.4;
}
