.table {
    border-radius: var(--radius);
    box-shadow: var(--box-shadow);
    margin: 40px 0;
}

.table__head {
    font-weight: bold;
    padding: 20px;
}

.table__head,
.table__body__item {    
    display: grid;
    grid-template-columns: 1fr 4fr repeat(3, 2fr);
    font-size: 18px;
    padding: 20px;
    text-align: center;
}

.table__body {
    padding-bottom: 10px;
}

.table__body__item {
    padding: 10px 20px;
}

.table__body__item div:nth-child(2) {
    text-align: left;
}

.table__body__item:nth-child(1),
.table__body__item:nth-child(3),
.table__body__item:nth-child(5),
.table__body__item:nth-child(7),
.table__body__item:nth-child(9) {
    background-color: rgba(22, 206, 127, 0.05);
}
