.chart-sankey {
    user-select: none;
    min-width: 600px;
    overflow: hidden;
    position: relative;
    padding: 20px;
}

.chart-sankey .chart__title {
    margin-bottom: 40px;
}

.chart-sankey__svg {
    display: block;
    margin: 0 auto;
    overflow: visible;
}

.chart-sankey .g-nodes__rect {
    cursor: move;
}

.chart-sankey .g-links__path {
    fill: none;
    mix-blend-mode: multiply;
}

.chart-sankey .chart__label {
    font-size: 10px;
    text-transform: uppercase;
}

.chart-sankey .chart-sankey__tooltip {
    background-color: var(--white);
    box-shadow: var(--box-shadow);
    border-radius: 8px;
    color: var(--black);
    display: flex;
    flex-direction: column;
    width: auto;
    white-space: nowrap;
    padding: 16px;
    position: absolute;
    top: 0;
    left: 0;
    text-transform: uppercase;
}

.chart-sankey .tooltip__name {
    color: var(--black);
    margin-right: 6px;
}

.chart-sankey .tooltip__value {
    font-weight: 600;
    flex-wrap: nowrap;
}

.chart-sankey .chart__label--name {
    font-size: 18px;
    font-weight: bold;
    text-transform: uppercase;
}

.chart-sankey .chart__label--value {
    font-size: 14px;
}
